import React from "react";
import { Container } from "react-bootstrap";
import { CardItem } from "../../components/CardItem/CardItem";
import "./Portofolio.scss";

// data
import PortofolioData from "./PortofolioList.json";

export const Portofolio = () => {
  const getPortofolioByCategory = (category) => {
    return PortofolioData.filter(
      (item) => item.category.toLowerCase() === category.toLowerCase()
    );
  };

  return (
    <div className="portofolio-page">
      <Container>
        <div className="portofolio-group">
          <h3>Game</h3>
          <div className="portofolio-list">
            {getPortofolioByCategory("game").map((item, index) => {
              return (
                <CardItem
                  key={"game-" + index}
                  img={item.image[0]}
                  name={item.title}
                  description={
                    item.description.length > 150
                      ? item.description.substring(0, 150) + "..."
                      : item.description
                  }
                />
              );
            })}
          </div>
        </div>
        <div className="portofolio-group">
          <h3>Website</h3>
          <div className="portofolio-list">
            <div className="portofolio-list">
              {getPortofolioByCategory("website").map((item, index) => {
                return (
                  <CardItem
                    key={"game-" + index}
                    img={item.image[0]}
                    name={item.title}
                    description={
                      item.description.length > 150
                        ? item.description.substring(0, 150) + "..."
                        : item.description
                    }
                  />
                );
              })}
            </div>
          </div>
        </div>
      </Container>
    </div>
  );
};
